<template>
  <div class="card text-white bg-secondary mb-3" style="max-height:100%">

  <v-wait for="myRunsharevolume">
    <template slot="waiting">
      <div>
        <img src="@/assets/loading.gif" />
        Create Volume...
      </div>
    </template>
  </v-wait>

      <b-container  class="container">
      <br>
        <b-row>
          <b-col cols="12">
					<ValidationProvider
            ref="deploy_stackname"
						name="VolumeName"
						rules="required|alpha_num_name"   
            v-slot="{ errors, ariaMsg, ariaInput,   valid, invalid }"
				  >
              <div class="input-group input-group-sm sm-3">
                <input type="text" 
						      name="StackName"
                  class="form-control" 
                  v-bind:class="{'is-valid': isDeployValid.stackname == 1, 'is-invalid': isDeployValid.stackname == 2}"
                  aria-label="Small" 
									aria-describedby="inputGroup-sizing-sm"
                  placeholder="Volume Name"
                  v-model="deploy.stackname"
									v-on:keyup="isValid('stackname')"
                  >
									<div class="input-group-append">
										<button
												class="ti-cloud-up btn btn-info"
												round
												type="button"
												title="Deploy"
  											@click="add_deploy()"
											> Create Volume
											</button>
									</div>
              </div>
					<span class="hybrid-field-error">{{ errors[0] }}</span>
					</ValidationProvider>
          </b-col>
        </b-row>

        <b-row>
          <b-col cols="5">
            <b>Network:</b> <span class="text-white"> {{ networklabel  }} </span>
          </b-col>
          <b-col cols="4">
							<div class="custom-control custom-radio custom-control-inline">
								<input type="radio" id="customRadioInline1readwrite1" name="customRadioInline1readwrite1" class="custom-control-input" 
                  :checked="deploy.readwrite == 'read'"
                  value="read"
                  v-model="deploy.readwrite"
								  @click="onselectrw('read')"
                >
								<label class="custom-control-label" for="customRadioInline1readwrite1">Read</label>
							</div>
							<div class="custom-control custom-radio custom-control-inline">
								<input type="radio" id="customRadioInline1readwrite2" name="customRadioInline1readwrite2" class="custom-control-input"
                  :checked="deploy.readwrite == 'write'"
                  value="write"
                  v-model="deploy.readwrite"
								  @click="onselectrw('write')"
                >
								<label class="custom-control-label" for="customRadioInline1readwrite2">Write</label>
							</div>
          </b-col>
          <b-col cols="3">
										<button
												class="btn btn-outline-warning btn-sm"
												round
												type="button"
												title="Close menou"
  											@click="close_deploy()"
											> Close menu
											</button>
          </b-col>

        </b-row>
        <br>
      </b-container>

  </div>
</template>
<script>
import store from '@/store/index'
import {mapState, mapGetters, mapActions,dispatch} from 'vuex'
import Vue from 'vue'
import DatePicker from 'vue2-datepicker'
import card from '@/components/Card.vue'
import {ApiConfig} from "@/config/index";
import { ValidationObserver, ValidationProvider, extend } from 'vee-validate';
import { required, alpha_num, between} from 'vee-validate/dist/rules';
import 'vue2-datepicker/index.css';

// No message specified.
extend('alpha_num', alpha_num);

extend('mybetween', between);
extend('mybetween', {
  message: 'The {_field_} field must be a number: 1-65535'
});

//extend('alpha_num_image', alpha_num);
extend('alpha_num_image', value => {
 var regex = new RegExp(/^[A-Za-z0-9\:\.\-\_\/]+$/, 'i'); 
	if(regex.test(value)){
    return true;
	}
  return 'The {_field_} field may contain alphabetic characters, numbers, colons, hyphens, slashes, dots and underscores'
});

extend('alpha_num_name', value => {
 if(value.length < 16){
   var regex = new RegExp(/^[A-Za-z0-9\_\.\-]+$/, 'i'); 
    if(regex.test(value)){
      return true;
    }
  }
  return 'The {_field_} field may contain alphabetic characters, hyphens, dots and numbers (< 16 )'
});

extend('alpha_num_cpu', value => {
 //var regex = new RegExp(/^(?:\d{1})?(?:\.\d{1})?$/); 
 var regex = new RegExp(/^(?!0\d)\d+(?:\.\d{1})?$/); 
	if(regex.test(value)){
    return true;
	}
  return 'The {_field_} field may contain numbers and dots e.g 0.5'
});


extend('alpha_num_memory', value => {
 var regex = new RegExp(/^[0-9]+$/, 'i'); 
	if(regex.test(value)){
    return true;
	}
  return 'The {_field_} field may contain numbers e.g 500'
});

extend('alpha_url', value => {
 var regex = new RegExp(/https?:\/\/(git\.)?[-a-zA-Z0-9@:%._\+~#=]{2,256}\.[a-z]{2,4}\b([-a-zA-Z0-9@:%_\+.~#?&//=]*)/,'i');
 //var regex = new RegExp(/^[0-9]+$/, 'i'); 
	if(regex.test(value)){
    return true;
	}
  return 'The {_field_} field may contain url'
});

// Override the default message.
extend('required', {
  ...required,
  message: 'This field is required'
});
export default {
  components: {
    DatePicker,
    ValidationProvider,
    card
	},
  props: {
  },
  data() {
   return{
	  isHybridError: false,
	  isHybridSuccess: false,
    token: '',
		timePickerOptions:{
				start: '00:00',
				step: '00:30',
				end: '23:30'
		},
    deploy:{
  		"readwrite":'read'
    },
    isDeployValid:{
  		"stackname":3
	    },
     wgserverup:'',
     wgserversettings:'',
    isDeployError:{
	    },
			network: {
				options: []
			},
      networklabel:''
		}
  },
	mounted() {
		this.$root.$on('wg_view_network_refresh', (v) => {
        this.network.network = v.Names
        this.networklabel = v.Networks
        console.log(' -from table raw1 '+JSON.stringify(v))
        console.log(' -from table raw1 '+JSON.stringify(this.network))
		})
	},
	created() {
    var url_string = window.location.href
    var url = new URL(url_string);
    this.token = url.searchParams.get("token");
    //console.log("token "+ this.token);

	},
    beforeDestroy () {
		  this.$root.$off('wg_view_network_refresh')
 },
  computed: {
	},

  methods: {
    onselectrw(val){
      this.$set(this.deploy, 'readwrite', val)
    },
    onselectclick(val){
      console.log(' - '+JSON.stringify(this.network))
		},
  	async close_deploy(){
       this.$root.$emit('venus_shared_volume_add_close')
    },
  	async add_deploy(){
      
      console.log(' select '+JSON.stringify(this.deploy))
      
			var o = Object.entries(this.deploy)
			var pvalid = new Promise((resolve, reject) => {
				o.forEach((value, index, array) => {
						this.isValid(array[index][0])
						if (index === array.length -1) resolve();
				});
			});

		pvalid.then(() => {
      (async () => {
          var isAllValid = true
          var isAllDateValid = true
					for (const [key, value] of Object.entries(this.isDeployValid)) {
						console.log(`${key}: ${value}`);
						if(value != 1){
              isAllValid = false
						}
					}
					if(isAllValid && isAllDateValid){ 
           
              var myvpn = {}
              myvpn.options     = this.deploy.readwrite
              myvpn.stackname   = this.deploy.stackname
              myvpn.network     = this.network.network
              this.$socket.client.emit('share_volume', myvpn);
            this.$wait.start('myRunsharevolume');
					}else{
							var info = "Missing required fields"
							this.$swal({
								type: 'Info',
								title: 'Info!',
								icon:'info',
								html: info,
								showCloseButton: true,
								showLoaderOnConfirm: false,
								allowOutsideClick: false,
								cancelButtonText: 'No, cancel!',
								showCancelButton: false,
								showLoaderOnConfirm: false,
								reverseButtons: true,
								focusCancel: true,
								confirmButtonText: 'Ok!'
							})

				 }
      })();
		});
			
		},
  	async showInfo(action){
			if(action == 'Networkname' ){
          var info=`<h5>To start, first put a  <b>name</b> or something that gives away the subject of your project.
          </h5>
          <br>
<br>
 The field may contain alphabetic characters, numbers, colons, hyphens, slashes, dots and underscores
`
		  }else if(action == 'stackname'){
          var info=`<h5>To start, we need to have a <b>Network Stack Name </b> <br>
<br> This will bring up all the services, volumes, networks and everything else <br> in an isolated environment.
</h6>
<br>
<br>
 The field may contain alphabetic characters, numbers, hyphens, dots and underscores
`
		  }else if(action == 'network'){
          var info=`<h5>To start, we need to have a <b>Network </b> <br>
<br> This will bring up all the services, volumes, networks and everything else <br> in an isolated network environment.
<br>
<br>
Services running inside any of this networks containers have access (not limited by any firewall) to all other services.
<br>
</h5>
<br>
<br>
 The field may contain alphabetic characters and numbers

`
		  }else if(action == 'url'){
          var info=`<h5><b>Git Repo url </b></h5>
<br> The location of the file describing your service. (most comonly README)</br> 
<br>
</br>
</h5>
<br>
<br>
 The field may contain any valid url BUT it must be under https://git.swarmlab.io
`
		  }else if(action == 'networkport'){
          var info=`<h5><b>Network Port </b></h5>
<br> 
<h5>
By default, when you create a container, <b>it does not publish any of its ports to the outside world. </b>
<br>
To make a port available to Services which are not connected to the Stack network, we use this port. 
</h5>
<br>
<h5>
<u>
To make a service available we have to know the port number used by these service inside the container.
</u>
<br>
<b>Please insert that here.</b>
</h5>
<br>
<br>
(The port for outside connections is automatically generated and you dont have to worry about it!)
<br>
<br>
 The field may contain numbers
`
		  }else if(action == 'cpu'){
          var info=`<h5>To start, we need to have a <b>CPU </b> Limit <br>
<br>  Limit the specific CPUs or cores a container can use
</h6>
<br>
<br>
The field may contain numbers and dots e.g 0.5
`
		  }else if(action == 'memory'){
          var info=`<h5>To start, we need to have a <b>Memory </b> Limit 
<br> <br>  Limit the specific Memoty a container can use <br> 
e.g 200
<br> 
(MB)
</h6>
<br>
<br>
<h6>
<i>
It is important <b>not</b> to allow a running container to consume too much of the host machine’s memory.
</i>
</h6>
<br>
<br>
 The field may contain numbers
`
		  }else if(action == 'name'){
          var info=`<h5>To start, we need to have a <b>Name </b><br> 
This name is only for your own use. The system completely disregards it!
</h5>
<br>
<br>
 The field may contain alphabetic characters and numbers
`
			}
          this.$swal({
            type: 'Info',
            title: 'Info!',
            icon:'info',
            html: info,
            showCloseButton: true,
            showLoaderOnConfirm: false,
            allowOutsideClick: false,
            cancelButtonText: 'No, cancel!',
            showCancelButton: false,
            showLoaderOnConfirm: false,
            reverseButtons: true,
            focusCancel: true,
            confirmButtonText: 'Ok!'
          })
		},
  	async isValid(f){

      // ------------
      // validate
      // ------------
     if(f == 'stackname'){
			  var field = await this.$refs.deploy_stackname.validate();
        if(field.valid == true){
        	this.isDeployValid.stackname = 1
				}else{
        	this.isDeployValid.stackname = 2
				}
     }
   }

  },


  actions: {


	},
  sockets: {
        async share_volume_res(val) {
            console.log('volumes socke '+JSON.stringify(val))
        },
        async share_volume_res_resclose(val) {
            console.log('volumes sockeit clos '+JSON.stringify(val))
            if(val.shareinfo){
              var obj1 = {}
                  obj1.shareinfo  = val.shareinfo
                  obj1.network    = val.network
                  var res1 = await store.dispatch('pipelineLLO/setsharevolume', obj1)
                  console.log(JSON.stringify(res1));
              //close menou 
              this.$root.$emit('venus_shared_volume_add_close')
              // refresh  table volume my refresh
              this.$root.$emit('venus_shared_volume_refresh')
              // refresh  table volumes from client
              var v = {}
              v.Names  = val.shareinfo.stackid
              this.$root.$emit('wg_view_volume_refresh',v)
            }
            this.$wait.end('myRunsharevolume');
        },
  }
};
</script>
