<template>
<div>

 <div class=" text-center">
  <label
    class="text-center align-middle"
      v-if="loaded"
  >
  Interface: {{wginterface.ip}}
  </label>
 </div>

   <reactive-line-chart 
      v-if="loaded"
      :chart-data="chartData"
      :options="options"
      :styles="myStyles"
   ></reactive-line-chart>
</div>
</template>

<script>
import ReactiveLineChart from "./Chart-line-template.vue";
import store from '@/store/index'
import {mapState, mapGetters, mapActions,dispatch} from 'vuex'
import Vue from 'vue'
import {ApiConfig} from "@/config/index";
export default {
name: 'LineChartContainer',
  components: {
    ReactiveLineChart
  },
  data() {
    return {
      loaded: false,
      width: 300,
      height: 300,
      wginterface: {},
      chartData: null,
      options: { 
          scales: {
              yAxes: [{
                  stacked: true,
                  lineTension: 1,
                  steppedLine: true,
                  gridLines: {
                      display: false
                  }
              }],
              xAxes: [{
                  distribution: "linear",
                      stacked: true,
                      min: 0,
                      beginAtZero: true,
                  gridLines: {
                      display: true
                  }
              }]
            },
            legend: {
                display: true
            },
            responsive: true,
            spanGaps: false,
            maintainAspectRatio: false
                }

    };
  },
  methods: {
    async generateData() {

        try {
          var obj1 = {}
          obj1.ip         = `${this.wginterface.ip}`
          obj1.stackid    = `${this.wginterface.stackid}`
          obj1.publickey  = `${this.wginterface.publickey}`
          var res1 = await store.dispatch('pipelineLLO/getswgdata', obj1)
          console.log('return from wgstats ' + JSON.stringify(res1))
          this.chartData = res1.data
          this.loaded = true
        } catch (e) {
          console.error(e)
        }
    },
  },
  mounted() {
		this.$root.$on('hybrid_wg_showstats', (data) => {
              var labels = []
              var datain = []
              var dataout = []
              var chartdata = {
                   labels: labels,
                   datasets: [
                       {
                         label: "In",
                         backgroundColor: "#f87979",
                         data: datain,
                         borderColor: '#249EBF',
                         pointBackgroundColor: 'white',
                         borderWidth: 1,
                         fill: false,
                         lineTension: 0,
                         steppedLine: true,
                         pointBorderColor: '#249EBF'
                       },
                       {
                         label: "Out",
                         backgroundColor: "#A5CC82",
                         data: dataout,
                         borderColor: '#A5CC82',
                         pointBackgroundColor: 'white',
                         fill: false,
                         borderWidth: 1,
                         steppedLine: true,
                         pointBorderColor: '#A5CC82'
                       }
                     ],
                 }
            this.wginterface = data
            this.chartData = chartdata
            this.generateData()
            console.log("stats  ");
		})
  },
  beforeDestroy () {
		this.$root.$off('hybrid_wg_showstats')
 },
  computed: {
     myStyles () {
          return {
            height: `${this.height}px`,
            position: 'relative'
          }
        }
  },
/*
  watch: {
    chartData () {
      this.$data._chart.update()
    }
  }
*/
};
</script>

