<template>
  <card class="card-user" style="max-height:100%">


  <v-wait for="myRunInstancetutormountumount">
    <template slot="waiting">
      <div>
        <img src="@/assets/loading.gif" />
        Enter Lab_room...
      </div>
    </template>
  </v-wait>

  <v-wait for="myRunInstancetutormountumount1">
    <template slot="waiting">
      <div>
        <img src="@/assets/loading.gif" />
        Disconnect...
      </div>
    </template>
  </v-wait>

   <b-container fluid class="bv-example-row">
          <div class="row text-center">
            <div class="col-12">
              <b>My Connections on Shared volumes</b>
            </div>
          </div>


		<div class="white h-100 flex-fixed-width-item"
			<vuetable 
				ref="vuetable"
				:key="vuetablekeybsharedvolumeclient"
				:api-url='apiurl'
				:api-mode="true"
				:http-options="httpOptions"
				:fields="fields"
				:item-actions="itemActions"
				:sort-order="sortOrder"
				:show-sort-icons="true"
				:multi-sort="multiSort"
				:per-page="perpage"
				pagination-path="links.pagination"
				:pagination-component="paginationComponent"
				:append-params="moreParams"
				wrapper-class="vuetable-wrapper"
				loading-class="loading"
				detail-row-id="id"
				@vuetable:pagination-data="onPaginationData"
				@vuetable:load-success="loadsuccess"
        @vuetable:load-error="onLoadError"
				:css="css.table"
				 >

        <div slot="actions" slot-scope="props">
            <button
              v-if="actionrowindex == props.rowIndex"
              class="ti-info btn btn-info btn-sm"
              title="Show directory"
              round
              @click="onAction('run-more', props.rowData, props.rowIndex)">
            </button>
            <button
              v-else
              class="ti-info btn btn-outline-secondary btn-sm"
              title="Show directory"
              round
              round
              @click="onAction('run-more', props.rowData, props.rowIndex)">
            </button>
            <button
              v-if="testactionrowindex[props.rowData.Names]"
              class="  ti-cloud-down   btn btn-outline-info btn-sm"
              title="unmount Shared Volume"
              round
              @click="onAction('run-umount', props.rowData, props.rowIndex)">
            </button>
            <button
              v-else
              class="ti-cloud btn btn-outline-secondary btn-sm"
              title="mount Shared Volume"
              round
              @click="onAction('run-mount', props.rowData, props.rowIndex)">
            </button>
            <button
              class="ti-trash  btn btn-outline-info btn-sm"
              title="Disconnect from server"
              round
              @click="onAction('stopclient', props.rowData, props.rowIndex)">
            </button>
				</div>
			</vuetable>
			<div class="vuetable-pagination ui basic segment grid">
				<vuetable-pagination-info 
					ref="paginationInfo"      
					:css="css.paginationInfo"
      		>
				</vuetable-pagination-info>
				<vuetable-pagination 
					:css="css.pagination"
					ref="pagination"
        	@vuetable-pagination:change-page="onChangePage"
      		>
				</vuetable-pagination>
			</div>
    </div>
   </b-container>
  </card>
</template>
<script>
import store from '@/store/index'
import {mapState, mapGetters, mapActions,dispatch} from 'vuex'
import Vue from 'vue'
import {Vuetable, VuetablePaginationDropdown} from 'vuetable-2'
import VuetablePaginationInfo from 'vuetable-2/src/components/VuetablePaginationInfo'
import VuetablePagination from 'vuetable-2/src/components/VuetablePagination'
import CssConfig from 'vuetable-2/src/components/VuetableCssConfig.js'
import MyVolume from "./deploy-volume.vue";
import card from '@/components/Card.vue'
import {ApiConfig} from "@/config/index";
export default {
  components: {
      card,
	    Vuetable,
     MyVolume,
			VuetablePagination,
			VuetablePaginationInfo,
			VuetablePaginationDropdown
	},
  props: {
  },
  data() {
   return{
    actionrowindex:'',
    actionrowindex1:'',
    actionaddshare:false,
    actionshowshare:false,
	  playbookInfo: {},
    testactionrowindex:{},
    info_nfsvolume:'',
    stopsharevolumeinfo:[],
    stopclientvolumeconnection:'',
    nfsinfo:{},
    token: '',
	  playbook: {
			'title':'',
			'name':'',
			'description':''
		},
			container:{
				name:'',
				view:0
			},
			pipeline:{},
 		selected: 'hybrid',
    options: [
      { text: 'Packages', value: 'packages' },
      { text: 'Images', value: 'images' },
      { text: 'Scripts', value: 'scripts' }
    ],
		showModal: false,
		visibility: [],
	 	active:false,
		vuetablekeybsharedvolumeclient:0,
		fielddata:{},
		fields: [ 
          {
        name: 'Names',
        title: '<span class="orange"></span>Name',
        sortField: 'Names',
        visible:true,
        dataClass: 'text-left text-wrap text-break break-word',
        width: '50%',
        callback: value => {
                (async() => {
		                var r = await this.checkactionrowindex(value)
                    if(r.data.data == 'yes'){
                      this.$set(this.testactionrowindex, value, true)
                      console.log('1 '+ this.testactionrowindex)
                    }else{
                      this.$set(this.testactionrowindex, value, false)
                      console.log('0 '+this.testactionrowindex)
                    }
                })()
                return value
        }
      },
      {
        name: 'Networks',
        title: '<span class="orange"></span>Network',
        //sortField: 'Image',
        visible:false,
        dataClass: 'left aligned w-25',
        width: '15%',
        callback: value => {
           return value
        }

      },
      {
        name: '_id',
        title: '<span class="orange"></span>mongo',
        visible:false
      },
      {
        name: 'Status',
        title: '<span class="orange"></span>Status',
        sortField: 'Status',
        titleClass: 'center aligned',
        visible:true,
        width: '10%'
      },
      {
        name: 'ID',
        sortField: 'ID',
        titleClass: 'center aligned',
        dataClass: 'left aligned w-25',
        visible:false,
        width: '15%'
      },
      {
          name: '__slot:actions',   // <----
          title: 'Actions',
          titleClass: 'center aligned',
          dataClass: 'center aligned',
          width: '15%'
        }
		],
		apiurl:ApiConfig.url_80+"/getshare_clientvolumes",
		//apiurl:ApiConfig.swarmlab_url_80+"/swarmlabhybridservices",
		css: CssConfig,
    perpage: 5,
    searchFor: '',
		sortOrder: [{
				field: 'Names',
				direction: 'asc'
		}],
    multiSort: true,
    paginationComponent: 'vuetable-pagination',
		itemActions: [
				{ name: 'view-item', label: '', icon: 'glyphicon glyphicon-zoom-in', class: 'btn btn-info', extra: {'title': 'View', 'data-toggle':"tooltip", 'data-placement': "left"} },
				{ name: 'edit-item', label: '', icon: 'glyphicon glyphicon-pencil', class: 'btn btn-warning', extra: {title: 'Edit', 'data-toggle':"tooltip", 'data-placement': "top"} },
				{ name: 'delete-item', label: '', icon: 'glyphicon glyphicon-remove', class: 'btn btn-danger', extra: {title: 'Delete', 'data-toggle':"tooltip", 'data-placement': "right" } }
		],
		moreParams: { 
				'filter':  '',
				'type':  'scripts',
				'tutor':  'yes'
			},
	 
		}
  },
	mounted() {
		this.$root.$on('venus_shared_clientvolume_refresh', (v) => {
        this.nfsinfo = v      
        this.refreshVuetable()
		})
		this.$root.$on('wg_view_network_refresh', (v) => {
        this.refreshVuetable()
		})
	},
	created() {
    var url_string = window.location.href
    var url = new URL(url_string);
    this.token = url.searchParams.get("token");
	},
    beforeDestroy () {
      this.$root.$off('venus_shared_clientvolume_refresh')
      this.$root.$off('wg_view_network_refresh')
 },
  computed: {
		httpOptions() {
			var token = this.token
			    var p="headers: {Authorization: token}}"; //table props -> :http-options="httpOptions"
			    return {headers: {Authorization: 'Bearer ' + token}} //table props -> :http-options="httpOptions"
	  },

	},

  methods: {
		async checkactionrowindex(value){
              var obj1 = {}
              obj1.name = `${value}`
              var res1 = await store.dispatch('pipelineLLO/getservicesmountstatus', obj1)
              return res1 
        },
   onError (type,description) {
                    var winfo=description
                    var info='<h5>Bootstrap '+type+'</h5>'
                    this.$swal({
                      type: type,
                      html: info+winfo,
                      showCloseButton: true,
                      showLoaderOnConfirm: false,
                      allowOutsideClick: false,
                      cancelButtonText: 'No, cancel!',
                      showCancelButton: false,
                      showLoaderOnConfirm: false,
                      reverseButtons: true,
                      focusCancel: true,
                      confirmButtonText: 'Ok!'
                      })
    },


		setRefresh () {
				this.moreParams = {
						'filter':  '',
						'type':    this.selected
				}
        Vue.nextTick( () => this.$refs.vuetable.refresh())
		},
		setFilter () {
				this.moreParams = {
						'filter':  this.searchFor,
						'type':    this.selected
				}
        Vue.nextTick( () => this.$refs.vuetable.refresh())
		},
		resetFilter () {
				this.moreParams = {}
				this.searchFor = ''
        Vue.nextTick( () => this.$refs.vuetable.refresh())
		},
   onPaginationData (paginationData) {
      this.$refs.pagination.setPaginationData(paginationData)
			this.$refs.paginationInfo.setPaginationData(paginationData) 
    },
    onChangePage (page) {
      this.$refs.vuetable.changePage(page)
    },
    editRow(rowData) {
      alert("You clicked edit on"+ JSON.stringify(rowData));
		},
    async onAction (action, data, index) {
			if(action == 'run-more' ){
          this.actionrowindex = index
          this.actionshowshare = true
          console.log('index '+JSON.stringify(data))
          this.$socket.client.emit('info_nfsvolume', data.Names);
			}else if(action == 'run-mount' ){
        this.$wait.start('myRunInstancetutormountumount');
            this.actionrowindex1 = index
           this.actionaddshare = true
          console.log('run mount '+JSON.stringify(data))
          this.$socket.client.emit('mount_nfsvolume', data.Names);
			}else if(action == 'run-umount' ){
        this.$wait.start('myRunInstancetutormountumount');
            this.actionrowindex1 = index
           this.actionaddshare = true
          console.log('run umount '+JSON.stringify(data))
          this.$socket.client.emit('umount_nfsvolume', data.Names);
			}else if(action == 'stopclient' ){
            this.stopclientvolumeconnection = data.Names
          var info='<h5>Disconnect from <b> '+ data.Names +'</b> <br><br> Users and networks associated with the stack will be removed. </h5>'
          this.$swal({
            type: 'Info',
            title: 'Info!',
            icon:'warning',
            html: info,
            showCloseButton: true,
            showLoaderOnConfirm: false,
            allowOutsideClick: false,
            cancelButtonText: 'No, cancel!',
            showCancelButton: true,
            showLoaderOnConfirm: false,
            reverseButtons: true,
            focusCancel: true,
            confirmButtonText: 'Ok!'
          }).then((result)=> {
            this.onAction_rm(result);
          })
            this.actionrowindex = index
            this.actionshowshare = true
            console.log('index '+JSON.stringify(data))
			}
    },
   async onAction_rm(action) {
        if(action.isConfirmed){
          this.$wait.start('myRunInstancetutormountumount1');
          this.$socket.client.emit('stop_client_volume_connection', this.stopclientvolumeconnection);
        }
   },
    refreshVuetable() {
			this.$nextTick(()=>{
					this.vuetablekeybsharedvolumeclient += 1
				})
      },
		playbookinfoShow(value) {
				return this.visibility[value]=true
		},
		playbookinfo(value) {
				return this.playbookInfo=value
		},
		rowClicked(row, event) {
			return {
				html: true,
				title: () => { return 'Hello <b>Popover:</b> ' + (++this.counter) },
				content: () => { return 'The date is:<br><em>' + new Date() + '</em>' }
			}
		},
		loadsuccess(response) {
       if(response.data != null){
				  var data = response.data.data
				  this.fielddata=data
				  var n = data.length
				  n=n-1
        }
			},
      onLoadError(payload) {
        if(payload.response.status == '401'){
          window.location.href = 'https://api-login.swarmlab.io:8089';
          Vue.nextTick( () => window.location.href = 'https://api-login.swarmlab.io:8089')
        }
      }
  },


  actions: {


	},
  sockets: {
        async mount_nfsvolume_res_resclose(val) {
            console.log('volumes socke '+JSON.stringify(val))
            this.refreshVuetable()
            this.$wait.end('myRunInstancetutormountumount');
        },
        async info_nfsvolume_res_resclose(val) {
            console.log('volumes info '+JSON.stringify(val.data))
            for (var i=0 ; i < val.data.length ; i++){
               console.log(val.data[i])
               if(val.data[i].Destination == '/mnt/share'){
                 this.info_nfsvolume = val.data[i].Source
                    var info=`<h3>Mounted on</h3><p class="text-success align-left"><pre>${this.info_nfsvolume}</pre></p>`
                    this.$swal({
                      icon:'info',
                      html: info,
                      showCloseButton: true,
                      showLoaderOnConfirm: false,
                      allowOutsideClick: false,
                      cancelButtonText: 'No, cancel!',
                      showCancelButton: false,
                      showLoaderOnConfirm: false,
                      reverseButtons: true,
                      focusCancel: true,
                      confirmButtonText: 'Ok!'
                      })
                      break;
               }else{
               }
            }
        },
        async stop_client_volume_connection_res(val) {
            console.log('volumes stop_share_volume_res '+JSON.stringify(val))
            this.stopsharevolumeinfo.push(val)
        },
        async stop_client_volume_connection_res_resclose(val) {
          this.$wait.end('myRunInstancetutormountumount1');
          this.refreshVuetable()
        },
  }

};
</script>
<style>
.flex-fixed-width-item {
    flex: 0 0 100px;
		}
.modalinfo {
  z-index: 10000000 !important;
	position:fixed;
}
