<template>
  <card class="card-user" style="max-height:100%">
      <div class="author"
             v-show="hybridshowlog==false"
      >
        <img class="avatar border-white" src="@/assets/img/cloudvolume--512.png" alt="...">
      </div>


  <v-wait for="myRunInstanceclientshare">
    <template slot="waiting">
      <div>
        <img src="@/assets/loading.gif" />
        Connect volume...
      </div>
    </template>
  </v-wait>

      <b-container fluid>
          <div class="row text-center"
             v-show="hybridshowlog==false"
          >
            <div class="col-12">
              <b>Shared volumes in <span class="text-info">{{ hybridnetwork.Names}}</span></b>
            </div>
          </div>

<br>
        <div class="row" 
          v-show="hybridshowlog"
        >
          <div class="col-12" >
           <log-view>
           </log-view>
          </div>
        </div>


        <div class="row" 
             v-if="hybridshowdata.stackid"
        >
          <div class="col-2 text-info" 
          >
          Options
          </div>
          <div class="col-3 text-secondary" >
          {{ hybridshowdata.data.shareinfo.options }}
          </div>
          <div class="col-7 text-info" >
                <button
                        class="btn btn-outline-info btn-sm"
                        round
                        type="button"
                        @click="onmount()"
                >
                  Connect_to {{ hybridshowdata.data.network }}
                </button>
                   
          </div>
        </div> <!-- row -->

      </b-container>
   <b-container fluid class="bv-example-row">

		<div class="input-group input-group-sm sm-3">
					
			<input type="text" 
				class="form-control" 
				aria-label="Small" aria-describedby="inputGroup-sizing-sm"
				placeholder="Search By StackName"
				v-model="searchFor"
				@keyup.enter="setFilter"
				>
					
			<div class="input-group-append">
				<button 
					class="btn btn-outline-primary" 
					round
					type="button"
					@click="setFilter">
					Go</button>
			</div>
					
			<div class="input-group-append">
				<button class="btn btn-outline-secondary" 
					round
					type="button"
					@click="resetFilter">
					Reset</button>
			</div>
    </div>
		<div class="white h-100 flex-fixed-width-item"
			<vuetable 
				ref="vuetable"
				:key="vuetablekeyd"
				:api-url='apiurl'
				:api-mode="true"
				:http-options="httpOptions"
				:fields="fields"
				:item-actions="itemActions"
				:sort-order="sortOrder"
				:show-sort-icons="true"
				:multi-sort="multiSort"
				:per-page="perpage"
				pagination-path="links.pagination"
				:pagination-component="paginationComponent"
				:append-params="moreParams"
				wrapper-class="vuetable-wrapper"
				loading-class="loading"
				detail-row-id="id"
				@vuetable:pagination-data="onPaginationData"
				@vuetable:load-success="loadsuccess"
        @vuetable:load-error="onLoadError"
				:css="css.table"
				 >

        <div slot="actions" slot-scope="props">
            <button
              v-if="actionrowindex == props.rowIndex"
              class="ti-more-alt btn btn-secondary btn-sm"
              round
              @click="onAction('run-more', props.rowData, props.rowIndex)">
            </button>
            <button
              v-else
              class="ti-more btn btn-outline-secondary btn-sm"
              round
              @click="onAction('run-more', props.rowData, props.rowIndex)">
            </button>
				</div>
			</vuetable>
			<div class="vuetable-pagination ui basic segment grid">
				<vuetable-pagination-info 
					ref="paginationInfo"      
					:css="css.paginationInfo"
      		>
				</vuetable-pagination-info>
				<vuetable-pagination 
					:css="css.pagination"
					ref="pagination"
        	@vuetable-pagination:change-page="onChangePage"
      		>
				</vuetable-pagination>
			</div>
    </div>
   </b-container>
  </card>
</template>
<script>
import store from '@/store/index'
import {mapState, mapGetters, mapActions,dispatch} from 'vuex'
import Vue from 'vue'
import {Vuetable, VuetablePaginationDropdown} from 'vuetable-2'
import VuetablePaginationInfo from 'vuetable-2/src/components/VuetablePaginationInfo'
import VuetablePagination from 'vuetable-2/src/components/VuetablePagination'
import CssConfig from 'vuetable-2/src/components/VuetableCssConfig.js'
import card from '@/components/Card.vue'
import LogView from "./LogView.vue";
import {ApiConfig} from "@/config/index";
export default {
  components: {
      card,
	    Vuetable,
			VuetablePagination,
			VuetablePaginationInfo,
			VuetablePaginationDropdown,
      LogView
	},
  props: {
  },
  data() {
   return{
    hybridshowlog:false,
    hybridshowuser:false,
    actionrowindex:'',
	  playbookInfo: {},
    hybridshowdata:{},
    hybridnetwork:{},
    token: '',
	  playbook: {
			'title':'',
			'name':'',
			'description':''
		},
			container:{
				name:'',
				view:0
			},
			pipeline:{},
 		selected: 'hybrid',
    options: [
      { text: 'Packages', value: 'packages' },
      { text: 'Images', value: 'images' },
      { text: 'Scripts', value: 'scripts' }
    ],
		showModal: false,
		visibility: [],
	 	active:false,
		vuetablekeyd:0,
		fielddata:{},
		fields: [ 
          {
        name: 'stackname',
        title: '<span class="orange"></span>Name',
        sortField: 'stackname',
        visible:true,
        dataClass: 'text-left text-wrap text-break break-word',
        width: '15%'
      },
      {
        name: 'stackid',
        title: '<span class="orange"></span>Network',
        sortField: 'stackid',
        visible:true,
        dataClass: 'left aligned w-25',
        width: '25%'

      },
      {
        name: '_id',
        title: '<span class="orange"></span>mongo',
        visible:false
      },
      {
        name: 'bootstrapdatebegin5',
        title: '<span class="orange"></span>Begin',
        sortField: 'bootstrapdatebegin5',
        titleClass: 'center aligned',
        visible:false,
        width: '10%'
      },
      {
        name: 'data.network',
        title: '<span class="orange"></span>ip',
        sortField: 'data.network',
        titleClass: 'center aligned',
        visible:true,
        width: '10%',
        callback: function(value) {
          //var val = JSON.parse(value)
          //var v = Object.keys(val);
          //var vv = v[0]
          //if(v){
              //return val[vv].IPAddress
          //}else{
              //return ''
          //}
          return value
        }
        
      },
      {
        name: 'ID',
        sortField: 'ID',
        titleClass: 'center aligned',
        dataClass: 'left aligned w-25',
        visible:false,
        width: '15%'
      },
      {
          name: '__slot:actions',   // <----
          title: 'Actions',
          titleClass: 'center aligned',
          dataClass: 'center aligned',
          width: '5%'
        }
		],
		apiurl:ApiConfig.swarmlab_url_80+"/getsharevolume",
		css: CssConfig,
    perpage: 5,
    searchFor: '',
    searchForStackid: '',
		sortOrder: [{
				field: 'bootstrapdatebegin5',
				direction: 'desc'
		}],
    multiSort: true,
    paginationComponent: 'vuetable-pagination',
		itemActions: [
				{ name: 'view-item', label: '', icon: 'glyphicon glyphicon-zoom-in', class: 'btn btn-info', extra: {'title': 'View', 'data-toggle':"tooltip", 'data-placement': "left"} },
				{ name: 'edit-item', label: '', icon: 'glyphicon glyphicon-pencil', class: 'btn btn-warning', extra: {title: 'Edit', 'data-toggle':"tooltip", 'data-placement': "top"} },
				{ name: 'delete-item', label: '', icon: 'glyphicon glyphicon-remove', class: 'btn btn-danger', extra: {title: 'Delete', 'data-toggle':"tooltip", 'data-placement': "right" } }
		],
		moreParams: { 
				'filter':  '',
				'stackid':  '',
				'type':  'scripts',
				'tutor':  'yes'
			},
	 
		}
  },
	mounted() {
		this.$root.$on('wg_view_volume_refresh', (v) => {
        this.hybridnetwork = v
			  this.searchForStackid = v.Names
        console.log(' v ' + JSON.stringify(v))
        this.setFilter()

        //Vue.nextTick( () => this.$refs.vuetable.refresh())
		})
	},
	created() {
    var url_string = window.location.href
    var url = new URL(url_string);
    this.token = url.searchParams.get("token");
    //console.log("token "+ this.token);

	},
    beforeDestroy () {
		//this.$root.$off('wg_refresh_bootstrap_view')
		this.$root.$off('wg_view_volume_refresh')
 },
  computed: {
		httpOptions() {
			var token = this.token
			    var p="headers: {Authorization: token}}"; //table props -> :http-options="httpOptions"
			    return {headers: {Authorization: 'Bearer ' + token}} //table props -> :http-options="httpOptions"
	  },

	},

  methods: {
   async onmount(action) {
			  console.log('mount '+ JSON.stringify(this.hybridnetwork ))
			  console.log('mount data '+ JSON.stringify(this.hybridshowdata ))
        var v = {}
        v.share_container = this.searchForStackid  // wg local container 
        var val = this.hybridshowdata.data.network.split('/')
         //console.log(this.hybridshowdata.data.network)
          //var v1 = Object.keys(val);
          //var vv1 = v1[0]
          //val[vv1].IPAddress
        v.share_ip = val[0]  // wg local
        //v.service = this.hybridnetwork  // service client
			  console.log('mount socket '+ JSON.stringify(v ))
        this.$socket.client.emit('connect_nfsvolume', v);
        this.$wait.start('myRunInstanceclientshare');
   },
   async onActionUser(action) {
      if(action == 1){
            this.hybridshowuser=true
            var data = {}
            data.hybridshowuser = this.hybridshowuser
            data.data = this.hybridshowdata
            this.$root.$emit('hybrid_wg_showuser',data)
      }else if(action == 2 ){
            this.hybridshowuser=false
            var data = {}
            data.hybridshowuser = this.hybridshowuser
            data.data = this.hybridshowdata
            this.$root.$emit('hybrid_wg_showuser',data)
       }
   },
   async onActionLog(action) {
      if(action == 1){
          var log = await store.dispatch("pipelineLLO/wg_servicelog",{
            token:this.token,
            bootstrapnameid:this.hybridshowdata.bootstrapnameid
          })
         // console.log('logs1 '+JSON.stringify(log))
        var data = log.data.data.data
        if(log.data.data.errorexec_msg == 'ok' && log.data.data.data){
            this.hybridshowlog=true
            this.$root.$emit('hybrid_server_log_in',data)
            //console.log('logsdata '+JSON.stringify(log.data.data.data))
         }else{
          var info = 'No logs are available'
          this.$swal({
            type: 'Info',
            title: 'LOG',
            icon:'info',
            html: info,
            showCloseButton: true,
            showLoaderOnConfirm: false,
            allowOutsideClick: false,
            cancelButtonText: 'No, cancel!',
            showCancelButton: false,
            showLoaderOnConfirm: false,
            reverseButtons: true,
            focusCancel: true,
            confirmButtonText: 'Ok!'
          })
         }
      }else if(action == 2 ){
        this.hybridshowlog=false
       }
   },
   async onActionHybrid(action) {

			if(action == 'stack-stop' ){
          var info='<h5>This will remove the network with the name<b> '+ this.hybridshowdata.bootstrapstackname +'</b> <br><br> Users, networks, and secrets associated with the stack will be removed. </h5>'
          this.$swal({
            type: 'Info',
            title: 'Info!',
            icon:'warning',
            html: info,
            showCloseButton: true,
            showLoaderOnConfirm: false,
            allowOutsideClick: false,
            cancelButtonText: 'No, cancel!',
            showCancelButton: true,
            showLoaderOnConfirm: false,
            reverseButtons: true,
            focusCancel: true,
            confirmButtonText: 'Ok!'
          }).then((result)=> {
            //console.log(JSON.stringify(result))
            this.onActionHybrid_stackrm(result);
          })

      }else if(action == 'service-stop' ){
				var log = await store.dispatch("pipelineLLO/hybrid_servicerm",{
					token:this.token,
					bootstrapstackid:this.hybridshowdata.bootstrapstackid,
					bootstrapnameid:this.hybridshowdata.bootstrapnameid
				})
        Vue.nextTick( () => this.$refs.vuetable.refresh())
      }else if(action == 'service-restart' ){

				var log = await store.dispatch("pipelineLLO/wg_servicerestart",{
              token:this.token,
              bootstrapnameid:this.hybridshowdata.bootstrapnameid
				})
        Vue.nextTick( () => this.$refs.vuetable.refresh())
          var info =''
        if(log.data.data.errorexec_msg == 'ok'){
          info ='<h5>Update existing service: ok!</h5>'
        }else{
          info ='<h5>Update existing service: error!</h5>'
        }
          this.$swal({
            type: 'Info',
            title: 'INFO',
            icon:'info',
            html: info,
            showCloseButton: true,
            showLoaderOnConfirm: false,
            allowOutsideClick: false,
            cancelButtonText: 'No, cancel!',
            showCancelButton: false,
            showLoaderOnConfirm: false,
            reverseButtons: true,
            focusCancel: true,
            confirmButtonText: 'Ok!'
          })

//console.log('stackid '+this.hybridshowdata.bootstrapnameid)
      }
   },
   async onActionHybrid_stackrm(action) {
            if(action.isConfirmed){
              var log = await store.dispatch("pipelineLLO/stackrmwg",{
                token:this.token,
                bootstrapstackid:this.hybridshowdata.bootstrapstackid
              })
              Vue.nextTick( () => this.$refs.vuetable.refresh())
            }
   },
   onError (type,description) {
                    var winfo=description
                    var info='<h5>Bootstrap '+type+'</h5>'
                    this.$swal({
                      type: type,
                      html: info+winfo,
                      showCloseButton: true,
                      showLoaderOnConfirm: false,
                      allowOutsideClick: false,
                      cancelButtonText: 'No, cancel!',
                      showCancelButton: false,
                      showLoaderOnConfirm: false,
                      reverseButtons: true,
                      focusCancel: true,
                      confirmButtonText: 'Ok!'
                      })
    },


		setFilter () {
				this.moreParams = {
						'filter':   this.searchFor,
						'stackid':  this.searchForStackid,
						'type':     this.selected
				}
        Vue.nextTick( () => this.$refs.vuetable.refresh())
		},
		resetFilter () {
				this.moreParams = {}
				this.searchFor = ''
        Vue.nextTick( () => this.$refs.vuetable.refresh())
		},
   onPaginationData (paginationData) {
      this.$refs.pagination.setPaginationData(paginationData)
			this.$refs.paginationInfo.setPaginationData(paginationData) 
    },
    onChangePage (page) {
      this.$refs.vuetable.changePage(page)
    },
    editRow(rowData) {
      alert("You clicked edit on"+ JSON.stringify(rowData));
		},
    async onAction (action, data, index) {
      this.actionrowindex = index
      //this.$root.$emit('hybrid_show_info',data)
      //console.log('index '+this.actionrowindex)
			if(action == 'view-item' ){
			}else if(action == 'run-more' ){
        this.hybridshowdata = data
        console.log('hybrid '+JSON.stringify(data))
			}else if(action == 'delete-item' ){
				this.$swal({
					type: 'info',
					html: info+winfo,
					showCloseButton: true,
					showLoaderOnConfirm: false,
					allowOutsideClick: false,
					 cancelButtonText: 'No, cancel!',
					showCancelButton: true,
					showLoaderOnConfirm: false,
					reverseButtons: true,
					focusCancel: true,
					confirmButtonText: 'Yes, Delete it!'
					})
			}else if(action == 'run-item' ){
			}
    },
    refreshVuetable() {
			this.$nextTick(()=>{
					this.vuetablekeyd += 1
				})
      },
		playbookinfoShow(value) {
				return this.visibility[value]=true
		},
		playbookinfo(value) {
				return this.playbookInfo=value
		},
		rowClicked(row, event) {
			return {
				html: true,
				title: () => { return 'Hello <b>Popover:</b> ' + (++this.counter) },
				content: () => { return 'The date is:<br><em>' + new Date() + '</em>' }
			}
		},
		loadsuccess(response) {
       if( response.data ){
         if( response.data.data ){
            var data = response.data.data
            this.fielddata=data
            var n = data.length
            n=n-1
          }else{
            var data = ''
            var n = data.length
            n=n-1
          }
        }
			},
      onLoadError(payload) {
          /*
          //error2 "invalid_token" join-service.vue:684
          //error2 "The access token provided has expired" join-service.vue:685
          //error2 "Unauthorized" join-service.vue:686
          //error2 401
        */
        if(payload.response.status == '401'){
          window.location.href = 'https://api-login.swarmlab.io:8089';
          Vue.nextTick( () => window.location.href = 'https://api-login.swarmlab.io:8089')
        }
      }


  },


  actions: {


	},
  sockets: {
        async connect_nfsvolume_res_resclose(val) {
            console.log('volumes socke '+JSON.stringify(val))
            this.$root.$emit('venus_shared_clientvolume_refresh',val)
            this.$wait.end('myRunInstanceclientshare');
        },
  }

};
</script>
