						<br>
						<br>

						<b>DISCLAMER:</b>
						<br>
						When connecting to other "Networks" <b>your host is becoming part of a network</b> of multiple other computers that are also connected.
						<br>
						This means that communication between those hosts is unlimited (no firewall).
						<br>
						<br>

						Since docker is a contained virtual environment this is <b>VERY SAFE</b> 
						<br>

						<br>
						<b>BUT</b>
						<br>
						to be absolutely secure, like when connecting to any network:
						<br>
							<ul>
