      </div>
    </template>
  </v-wait>


      <b-container fluid class="bv-example-row">
<!-- MENOU -->
          <div class="input-group input-group-sm sm-3">
              <div class="input-group-prepend">
                      <button
                              v-if="$socket.disconnected"
                              class="ti-unlink btn btn-outline-danger"
