                      >
                              </button>
              </div>
              <div class="input-group-prepend">

                      <b-dropdown 
                          v-if="hybridshowdata.Names"
                          v-model="ddTestVm.ddTestSelectedOption"
                          size="sm" 
                          text="Share"
                          variant="btn btn-outline-success"
                          v-on:change="changeItem">
                          <b-dropdown-item disabled value="0">Select Network</b-dropdown-item>
                          <b-dropdown-item 
                              v-for="option in ddTestVm.options" 
                              :key="option.value" 
                              :value="option.value"
                              @click="onselect(option.value)"
                          >
                                  {{option.text}}
                          </b-dropdown-item>           
                      </b-dropdown> 
                      <button
                              v-else
                              class="btn btn-outline-secondary"
                              round
                              type="button"
      												disabled
                      >
                              Connect</button>



                      <button
                              v-if="hybridshowdata.Names"
                              class="btn btn-outline-danger"
                              round
                              type="button"
                              @click="ondefault('container-default')"
                      >
                              Default</button>
                      <button
                              v-else
                              class="btn btn-outline-secondary"
                              round
                              type="button"
      												disabled
                      >
                              Default</button>
              </div>

              <div class="input-group-append">
                      <button
                              v-if="hybridshowdata.Names"
                              class="btn btn-outline-warning"
                              round
                              type="button"
                              @click="onAction('container-info')"
                      >
                              Info</button>
                      <button
                              v-else
                              class="btn btn-outline-secondary"
                              round
                              type="button"
      												disabled
                      >
                              Info</button>
              </div>
          </div>
<!-- MENOU -->

      </b-container>

<!-- MENOUDESC -->
      <b-container fluid>

        <div class="row" >
          <div class="col-3 text-info" >
            ID 
          </div>
          <div class="col-9 text-secondary" >
            {{ hybridshowdata.ID }}
          </div>
         </div>

        <div class="row" >
          <div class="col-3 text-info" >
            Image          
          </div>
          <div class="col-9 text-secondary" >
            {{ hybridshowdata.Image }}
          </div>
        </div> <!-- row -->

        <div class="row" >
          <div class="col-3 text-info" >
            Names
          </div>
          <div class="col-9 text-secondary" >
            {{ hybridshowdata.Names }}
          </div>
        </div> <!-- row -->

        <div class="row" >
          <div class="col-3 text-info" >
            RunningFor
          </div>
          <div class="col-9 text-secondary" >
            {{ hybridshowdata.RunningFor }}
          </div>
        </div> <!-- row -->

        <div class="row" >
          <div class="col-3 text-info" >
            Status
          </div>
          <div class="col-9 text-secondary" >
           {{ hybridshowdata.Status }}
          </div>
        </div> <!-- row -->

        <div class="row" >
          <div class="col-3 text-info" >
            CreatedAt
          </div>
          <div class="col-9 text-secondary" >
            {{ hybridshowdata.CreatedAt }}
          </div>
        </div> <!-- row -->

        <div class="row" >

          <div class="col-3 text-info" >
                      <button
                              v-if="hybridshowdata.Networks&&addNetworkMenou==false"
                              class="btn btn-outline-info  btn-sm"
                              round
                              type="button"
                              @click="addNetwork(hybridshowdata,'on')"
                      >
                              Networks</button>
                      <button
                              v-else
                              class="btn btn-outline-success  btn-sm"
                              round
                              type="button"
                      >
                              Shared </button>
          </div>
          <div class="col-9 text-secondary" >
          {{ hybridshowdata.Networks }}
          </div>
        </div>

        <div class="row" >
          <div class="col-3 text-info" >
          Ports
          </div>
          <div class="col-9 text-secondary" >
          {{ hybridshowdata.Ports }}
          </div>
        </div> <!-- row -->
      </b-container>
<!-- MENOUDESC -->




  </card>
</template>
<script>
import store from '@/store/index'
import {mapState, mapGetters, mapActions,dispatch} from 'vuex'
import Vue from 'vue'
import card from '@/components/Card.vue'
import {ApiConfig} from "@/config/index";
import { extend } from 'vee-validate';
import { required, alpha_num } from 'vee-validate/dist/rules';

// No message specified.
extend('alpha_num', alpha_num);

// Override the default message.
extend('required', {
  ...required,
  message: 'This field is required'
});

export default {
   components: {
     card
   },
   data () {
      return {
        loading: false,
        addNetworkMenou:false,
        // info for network
        dockercompose:{}, // dockercompose app info
        webinterface:{},  
        hybridshowdata:{}, // app info from table
        virtualnetwork:{}, // network to attache 
        virtualnetworkinfo:'',
        virtualportinfo:{
              ports:''
        },
        ddTestVm: {
                    originalValue: [],
                    ddTestSelectedOption: "Value1",
                    disabled: false,
                    readonly: false,
                    visible: true,
                    color: "",
                    options: []
            }
      }
    },
  methods: {
    changeItem(val){
    },
    onselect(val){
      this.$wait.start('myRunshare');
      this.virtualnetwork.network = val
          var v = {}
          v.s = this.hybridshowdata
          v.c = this.dockercompose
          v.n = this.virtualnetwork.network
          this.$socket.client.emit('share_app', v);
      console.log('share '+ JSON.stringify(val))
    },
    ondefault(val){
      this.$wait.start('myRunshare');
          var v = {}
          v.s = this.hybridshowdata
          v.c = this.dockercompose
          v.n = this.virtualnetwork.network
          this.$socket.client.emit('share_app_default', v);
      console.log('default '+ JSON.stringify(val))
    },
		refreshsharedtable () {
      this.$root.$emit('venus_shared_apps_refresh')
    },
    async onAction (action, data, index) {
			if(action == 'container-connect' ){

/*
          var v = {}
          v.s = this.hybridshowdata
          v.c = this.dockercompose
          this.$socket.client.emit('share_app', v);
          console.log('index '+JSON.stringify(v))
*/
			}else if(action == 'container-default' ){
			}else if(action == 'container-info' ){
          var v = {}
          v.s = this.hybridshowdata
          v.c = this.dockercompose
          console.log(v)
          this.$socket.client.emit('container_info', v);
			}
    },
  },
  mounted() {
		this.$root.$on('venus_shared_apps_menou', (data) => {
      this.hybridshowdata = data
      this.addNetworkMenou = false
      this.$socket.client.emit('share_app_info', this.hybridshowdata);
      var v = {}
      v.name = data.Names
      this.$socket.client.emit('virtual_port_getinfo', v);
		})
  },
  beforeDestroy () {
    this.$root.$off('venus_shared_apps_menou')
 },


    created () {
    },
     sockets: {
        async virtual_port_info_res(val) {
            console.log('container ports '+JSON.stringify(val))
            var lines = val.data.split(/\r\n|\n\r|\n|\r/);
            var portsinfo = '<br><b>Open Ports</b><br><table class="table table-striped">'
            for (var i=0 ; i < lines.length ; i++){
               portsinfo += `<tr>`
               portsinfo += `<td>${lines[i]}</td>`
               portsinfo += `</tr>`
            }
            portsinfo += `</table>`
            this.$set(this.virtualportinfo, 'ports', portsinfo)
        },
        async virtual_port_info_reserror(val) {
            console.log('container port error '+JSON.stringify(val))
        },
        async container_info_reserror(val) {
            console.log('container infoerror '+JSON.stringify(val))
          var info = `<h5>Network Interfaces</h5><div style="text-align:center;"> 
             <b>cannot find information for this service</b>
            <br>
            <br>
            Open a terminal and try the following commands:
            <br>
            <br>
            <p class="text-success">
              ${val.command}
            </p>
          </div><br>`
          this.$swal({
            title: 'Info',
            icon:'info',
            html: info,
            showCloseButton: true,
            showLoaderOnConfirm: false,
            allowOutsideClick: false,
            cancelButtonText: 'No, cancel!',
            showCancelButton: false,
            showLoaderOnConfirm: false,
            reverseButtons: true,
            focusCancel: true,
            confirmButtonText: 'Ok!'
          })
        },
        async container_info_res(val) {
            console.log('container info '+JSON.stringify(val))
            var d = JSON.parse(val.data)
            console.log('container info1 '+JSON.stringify(d))

            var infotable = '<table class="table table-striped">'
            infotable += `<tr>`
            infotable += `<td>Interface</td>`
            infotable += `<td>IP</td>`
            infotable += `<td>Mask</td>`
            infotable += `</tr>`
            var netipjupyter=''
            var netipeth0=''
            for (var i=0 ; i < d.length ; i++)
            {
                infotable += `<tr>`
                infotable += `<td>${d[i].ifname}</td>`
                infotable += `<td>${d[i].addr_info[0].local}</td>`
                infotable += `<td>${d[i].addr_info[0].prefixlen}</td>`
                infotable += `</tr>`
                var grep = new RegExp('swlab');
                if(grep.test(d[i].ifname)){
                  netipjupyter = d[i].addr_info[0].local
                }
                var grepeth0 = new RegExp('eth0');
                if(grepeth0.test(d[i].ifname)){
                  netipeth0 = d[i].addr_info[0].local
                }
 
            }
            infotable += '</table>'

          if(val.jupyter){
            var dd = JSON.parse(val.jupyter)
            var infotable1 = '<table class="table table-striped">'
            var jupyterurl =  `<a href="http://0.0.0.0:8888/lab?token=${dd.token}" target="_new">click here to go to Jupyter</a>`

            infotable1 += `<tr>`
            infotable1 += `<td><b>URL to connect to Jupyter</b></td>`
            infotable1 += `</tr>`
            infotable1 += '</table>'

                infotable1 += `replace  <b>0.0.0.0</b> with ip address from <b>eth0</b>`
                infotable1 += `<br>try to find</br>`
                infotable1 += `<span><pre>http://${netipeth0}:8888/lab?token=${dd.token}</pre></span>`

            infotable1 += `<br>`
            infotable1 += '<table class="table table-striped"'
            infotable1 += `<tr>`
            infotable1 += `<td><b>URL to send to participants</b></td>`
            infotable1 += `</tr>`
                infotable1 += '</table>'
                infotable1 += `<br>replace  <b>0.0.0.0</b> with ip address from <b>swlab*</b>`
                infotable1 += `<br>try to find</br>`
                infotable1 += `<span><pre>http://${netipjupyter}:8888/lab?token=${dd.token}</span></pre>`
                console.log('container info2 '+JSON.stringify(dd))
                var info = `<h5>Network Interfaces</h5><div style="text-align:left;"> ${infotable}</div><br> ${jupyterurl} <br><br> ${infotable1} <br><h5>${this.virtualportinfo.ports}</h5>`
              }else{
                var info = `<h5>Network Interfaces</h5><div style="text-align:left;"> ${infotable}</div><br> <h5>${this.virtualportinfo.ports}</h5>`
              }
          console.log('container info1 '+JSON.stringify(info))
          this.$swal({
            title: 'Info',
            icon:'info',
            html: info,
            showCloseButton: true,
            showLoaderOnConfirm: false,
            allowOutsideClick: false,
            cancelButtonText: 'No, cancel!',
            showCancelButton: false,
            showLoaderOnConfirm: false,
            reverseButtons: true,
            focusCancel: true,
            confirmButtonText: 'Ok!'
          })

         },
        async share_app_exec_res(val) {
            console.log('exec data'+JSON.stringify(val))
            if(val.exec == "sharedefault"){
                console.log('exec default '+JSON.stringify(val))
            }else{
                console.log('exec no default '+JSON.stringify(val))
            }
        },
        async share_app_exec_resclose(val) {
            
            if(val.compose == "no"){
                console.log('exec no compose '+JSON.stringify(val))
            }else{
                console.log('exec yes compose '+JSON.stringify(val))
            }
            this.refreshsharedtable()
            this.$wait.end('myRunshare');
        },
        async share_app_exec_reserror(val) {
            console.log('exec error '+JSON.stringify(val))
        },
        async share_app_res(val) {
          if(val.exec == 'inspect'){
            var parsedata = JSON.parse(val.data)
            this.dockercompose.stack    = parsedata['com.docker.compose.project']
            this.dockercompose.service  = parsedata['com.docker.compose.service']
            this.dockercompose.file     = parsedata['com.docker.compose.project.config_files']
            this.dockercompose.env      = parsedata['com.docker.compose.project.environment_file']
            this.dockercompose.dir      = parsedata['com.docker.compose.project.working_dir']
            console.log(val)
          } else if(val.exec == 'ps'){
            console.log('--- '+JSON.stringify(val.data))
            var datalenth = val.data.length
            this.ddTestVm.options = [] 
            for (var i=0 ; i < datalenth ; i++)
            {
                    this.ddTestVm.options.push({
                            "value": val.data[i].Names,
                            "text": val.data[i].Networks
                        })
            }
          }else{
            console.log(val)
          }
        },
    }
};
</script>
