        width: '10%'
      },
      {
        name: 'bootstrapdateend',
        title: '<span class="orange"></span>End',
        sortField: 'bootstrapdateend',
        titleClass: 'center aligned',
        visible:true,
        width: '10%'
        /*
        callback: function(value) {
          if (value == true) {
              var v = '<span class="ti-check text-success"> </span>'
              return v
          }else{
              var v = '<span> </span>'
              return v
          }
        }
        */
      },
      {
