                      aria-label="Small" aria-describedby="inputGroup-sizing-sm"
                      placeholder="Name"
                      v-model="nodename"
                    >
                </div> <!-- coll -->

                <div class="col-3  pr-md-1" >



                   <ValidationProvider name="nodeid" rules="required|alpha_num"  ref="provider" 
